   .navbar {
    position: fixed;
    top: 0;
    width: 100%;
    padding: 5px 10px;
    z-index: 1000;
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
    display: flex;
    background-color: rgba(3, 10, 11, 0.5);
    align-items: center;
    justify-content: space-between;
  }
  
  .logo {
    font-size: 1.8rem;
    color: white;
    font-weight: bold;
    margin-left: 40px;
    font-family: serif;
    font-size: 30px;
  }
  
  .nav-links {
    list-style: none;
    display: flex;
    gap: 50px;
    margin-right: 90px;
    font-size: 10px;
  }
  
  .nav-links li {
    font-size: 1.2rem;
  }
  
  .nav-links a {
    text-decoration: none;
    color: white;
    transition: color 0.3s ease;
    font-family: serif;
  }
  
  .nav-links a:hover {
    color: #ffa500; /* Highlight color for hover effect */
  }
  
  /* Transparent Navbar */
  .navbar-transparent {
    background-color: transparent;
    box-shadow: none;
  }
  
  /* Black Navbar */
  .navbar-black {
    background-color: black;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  